/*starts custom css */

.content {
	padding: 50px;
}

/*ends custom css */

/**
 * border radius
 */

body {
	font-family: 'lato';
}

.card {
	width: 80%;
	margin: 0 auto;
}

.card-header {
	color: rgb(198, 18, 52);
}

.btn,
.jumbotron,
.nav :hover {
	border-radius: 0px;
}

/* cart badge */

.cart-badge,
.cart-badge:hover {
	border-radius: 50%;
	padding: 2px;
	font-size: 12px;
	font-style: italic;
	background: #000;
}
/**
 * single product page - product name
 */

.name {
	background: indigo;
	color: #fff;
	font-weight: bold;
}

a {
	color: rgb(216, 28, 75);
}

/* top up thumb */
.center-flex {
	justify-content: center;
}
.center-flex .product-img {
	width: 100px;
	text-align: center;
	margin: 0 auto;
}
.thumb-show {
	text-align: center;
}

/* black shade form 10-1 */
.black-10 {
	background: #f2f2f2;
}
.black-9 {
	background: #e6e6e6;
}
.black-8 {
	background: #d9d9d9;
}
.black-7 {
	background: #cccccc;
}
.black-6 {
	background: #bfbfbf;
}
.black-5 {
	background: #b3b3b3;
}

/**
 * product image on card
 */

.product-img {
	min-height: 100px;
}

.jumbotron h2 {
	margin-top: -20px;
}

@media only screen and (max-width: 600px) {
	.jumbotron h2 {
		margin-top: 10px;
	}
}

/**
 * jumbotron animation
 */

.jumbotron {
	height: 180px;
	color: #fff;
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	-webkit-animation: Gradient 15s ease infinite;
	-moz-animation: Gradient 15s ease infinite;
	animation: Gradient 15s ease infinite;
}

.all-form {
	width: 300px;
	margin: 3px;
	padding: 0 0 4px 0;
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@-moz-keyframes Gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
